let mockedClipboard = {};
jest.mock('@ali/clipboard-manager', () => {
  return jest.fn().mockImplementation(() => {
    return {
      write: (data) => {
        mockedClipboard = data;
      },
    };
  });
});

const { MIME_TYPES } = constants;
const { encodeFragment } = transferUtils;

const plgs = createCustomMoPlugins({
  factories: [moCard, moHeading, moRefBlock],
});

const serializer = createSerializer(plgs);

const DEFAULT_VALUE = (
  <value>
    <document>
      <block type="paragraph">
        <cursor />
      </block>
    </document>
  </value>
) as unknown as Value;

const VoidPlugin = () => ({
  schema: {
    blocks: {
      void: {
        isVoid: true,
      },
    },
  },
});

const CodePlugin = () => ({
  queries: {
    shouldSelectAll: (controller) => {
      const { selection, document } = controller.value;
      const { focus } = selection;
      const code = document.getClosest(
        focus.key,
        (node) => Block.isBlock(node) && node.type === 'code',
      );
      return code ? true : undefined;
    },
  },
});

interface PasteTesterConfig {
  onCangjiePaste: (event: CangjieClipboardEvent) => void;
}

const PasteTesterPlugin: (config: {
  pasteTester?: PasteTesterConfig;
}) => Plugin = (config) => {
  const { onCangjiePaste = noop } = config.pasteTester || {};
  return {
    onCangjiePaste: (event, controller, next) => {
      onCangjiePaste(event);
      return next();
    },
  };
};

interface ControllerConfig {
  refblock?: Partial<BiPluginConfig['refblock']>;
  clipboard?: Partial<BiPluginConfig['clipboard']>;
  pasteTester?: Partial<PasteTesterConfig>;
}

function createController(
  value?: Value,
  config?: ControllerConfig,
  options?: Partial<ControllerOptions>,
) {
  const { refblock = {}, clipboard, pasteTester } = config || {};
  const plugins = [
    DraggablePlugin({}),
    PasteTesterPlugin({
      pasteTester: { onCangjiePaste: noop, ...pasteTester },
    }),
    ...createCustomBiPlugins({
      factories: [biFactory],
      locales: {},
      configs: {
        clipboard: { serializer, ...clipboard },
        refblock: {
          locale: {
            nestedNotSupported: '',
            createFailed: '',
            referFromTitle: '',
            detach: '',
            detachFailed: '',
            detachSuccess: '',
            deleted: '',
            forbidden: '',
            invalid: '',
            referenceListTitle: '[n][m]',
            copySuccess: '',
            cannotMoveTo: '',
            cannotDropTo: '',
            copy: '',
            showRefs: '',
            creating: '',
            noRefers: '',
          },
          decorateRefBlockFragment: (fragment) => fragment,
          getDocKey: () => 'host',
          renderReadOnly: () => null,
          generateDoc: () =>
            Promise.resolve().then(() => ({
              docKey: 'doc-a',
              uuid: 'doc-a',
            })),
          generateCopyDoc: (docKey) => {
            return Promise.resolve().then(() => ({
              uuid: 'abc',
              docKey,
            }));
          },
          getReferenceList: () => Promise.resolve().then(() => []),
          detach: () => Promise.resolve().then(),
          renderRefBlockInOtherStates: () => null,
          onRefBlockMounted: () => {},
          cacheHetuWhenCopy: () => '',
          getHetuWhenPaste: () => {},
          getRefBlockURL: (id) => id,
          getDocURL: (url) => url,
          ...refblock,
        },
      },
    }),
    moCard({}),
    moHeading({}),
    VoidPlugin(),
    CodePlugin(),
  ];

  return Controller.create(
    {
      plugins,
      value: value || DEFAULT_VALUE,
      autoFocus: true,
    },
    { normalize: false, ...options },
  );
}

