    test('#onKeyDown: select all inside a refblock', async () => {
      const controller = createController(
        (
          <value>
            <document>
              <block type="refblock" data={{ docKey: 'a' }}>
                <block type="paragraph">
                  Hel
                  <anchor />
                  lo
                </block>
                <block type="paragraph">
                  Worl
                  <focus />d
                </block>
              </block>
            </document>
          </value>
        ) as unknown as Value,
      );

      const expected = (
        <value>
          <document>
            <block type="refblock" data={{ docKey: 'a' }}>
              <block type="paragraph">
                <anchor />
                Hello
              </block>
              <block type="paragraph">
                World
                <focus />
              </block>
            </block>
          </document>
        </value>
      ) as unknown as Value;

      controller.run(
        'onKeyDown',
        new KeyboardEvent(
          'keydown',
          getHotkeyEvent('mod+a'),
        ) as unknown as React.KeyboardEvent,
      );
      controller.flush();

      expect(
        controller.value.toJSON({
          preserveSelections: true,
        }),
      ).toEqual(expected.toJSON({ preserveSelections: true }));
    });

